.dark-mode .not-found {
  pointer-events: none;
  color: linear-gradient(
    to top,
    rgba(var(--bs-dark-rgb), 0),
    rgba(var(--bs-dark-rgb), 1) 90%
  );
}

.not-found {
  pointer-events: none;
  color: linear-gradient(
    to top,
    rgba(var(--bs-light-rgb), 0),
    rgba(var(--bs-light-rgb), 1) 90%
  );
}
